<template>
  <div class="fengshui--date has-text-centered">
    <div class="control" v-for="(date, idx) in inputs" :key="idx">
      <input
        autocomplete="off"
        type="number"
        :ref="'dateType-'+idx"
        :class="'input '"
        :id="date.type"
        v-model="date.value"
        :placeholder="date.placeholder"
        :disabled="!date.active"
        @blur="checkMaxDate(idx, $event)"
        @input="checkDateInput(idx, $event)"
        @keydown="switchInputs(idx, $event)"
      >
      <label :for="date.type">{{ date.type }}</label>
      <span class="line"></span>
    </div>
    <transition name="fade">
      <div class="sign" v-show="signImage !== ''">
        <p>
          You're a
          <span class="color--pri">{{ sign }}</span>
        </p>
        <img
          width="125"
          v-if="sign !== '' && sign !== undefined"
          :src="`./1x/${signImage}x.png`"
          alt="chinese sign"
        >
      </div>
    </transition>
    <div class="submit" v-if="button">
      <button class="hov" @click="getSign()">Check your chinese sign!</button>
    </div>
  </div>
</template>
<style lang='scss'>
$input-m: 42px;
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.4s, transform 0.6s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
  transform: translateY(10px);
}
.color {
  &--pri {
    color: $primary;
    font-weight: 600;
  }
}
.sign {
  margin-bottom: 50px;
}
.fengshui--date {
  text-align: center;
  margin: 0 auto;
  .control {
    width: 33.33%;
    max-width: 100px;
    margin: $input-m;
    display: inline-block;
    position: relative;
    &:nth-child(2),
    &:last-child {
      max-width: 50px;
    }
    &:nth-child(2) {
      &:before,
      &:after {
        content: "";
        position: absolute;
        width: 2px;
        height: 50px;
        background-color: rgba($dark, 0.3);
        left: -$input-m;
        top: 5px;
        transform: rotate(10deg);
      }
      &:after {
        right: -$input-m;
        left: unset;
      }
    }
    input {
      text-align: center;
      font-size: 1.4rem;
      padding: 0;
      border: none;
      box-shadow: none;
      &::-webkit-outer-spin-button,
      &::-webkit-inner-spin-button {
        display: none;
        -webkit-appearance: none;
      }
      & ~ label {
        position: absolute;
        top: -28px;
        text-transform: capitalize;
        left: 0;
      }
      & ~ .line {
        position: absolute;
        left: 0;
        bottom: 0.5ch;
        width: 100%;
        height: 2px;
        background-color: rgba($dark, 0.3);
        &:after {
          content: "";
          position: absolute;
          width: 100%;
          height: 2px;
          background-color: $primary;
          left: 0;
          bottom: 0;
          transform: scaleX(0);
          transition: transform 0.3s ease;
        }
      }

      &:focus,
      &:not(:placeholder-shown) {
        & ~ .line:after {
          transform: scaleX(1);
        }
      }
      &.set {
        background: rgba($primary, 0.2);
      }
    }
  }
}
</style>
<script>
export default {
  props: {
    button: Boolean
  },
  data() {
    return {
      sign: "",
      inputs: {
        0: {
          type: "year",
          active: true,
          value: "",
          maxVal: 2019,
          maxDigit: 4,
          placeholder: "YYYY"
        },
        1: {
          type: "month",
          active: true,
          value: "",
          maxVal: 12,
          maxDigit: 2,
          placeholder: "MM"
        },
        2: {
          type: "day",
          active: true,
          value: "",
          maxVal: 31,
          maxDigit: 2,
          placeholder: "DD"
        }
      }
    };
  },
  mounted() {
    console.log(" component mounted!");
  },
  methods: {
    checkDateInput(idx, input) {
      const context = this.inputs[idx];

      if (!context.value.toString().match(/[0-9]/)) return;

      if (
        (context.value !== "" &&
          context.value.split("").length == context.maxDigit &&
          input.keyCode !== 8) ||
        input.keyCode === 13
      ) {
        const next = parseInt(idx) + 1;
        idx == 2
          ? this.$refs["dateType-" + 0][0].focus()
          : this.$refs["dateType-" + next][0].focus();
      }
    },
    checkMaxDate(idx, input) {
      const context = this.inputs[idx];
      if (context.value === "") return;
      context.value =
        parseInt(context.value) < 10 && context.value.indexOf("0") === -1
          ? "0" + context.value
          : context.value;
      if (
        parseInt(context.value) > context.maxVal ||
        context.value.toString().split("").length < context.maxDigit
      )
        context.value = context.maxVal;
    },
    switchInputs(idx, input) {
      if (input.keyCode === 13) {
        const next = parseInt(idx) + 1;
        idx == 2
          ? this.$refs["dateType-" + 0][0].focus()
          : this.$refs["dateType-" + next][0].focus();
      }
    },
    getDate() {
      return {
        year: this.inputs[0].value,
        month: this.inputs[1].value,
        day: this.inputs[2].value
      };
    },
    getSign() {
      const date = this.getDate();
      console.log();
      if (this.validateDate(date)) {
        axios
          .get(`/profile/${date.year}-${date.month}-${date.day}`)
          .then(response => (this.sign = response.data.sign, console.log(response)));
      }
      this.$emit("getSign", this.sign);
    },
    validateDate(date) {
      if ((date.year == "", date.month == "" && date.day == "")) {
        return false;
      }
      return true;
    }
  },
  computed: {
    signImage() {
      return this.sign !== undefined ? this.sign.toLowerCase() : "";
    }
  }
};
</script>