<template>
  <div class="fengshui--love has-text-centered">
    <div class="human01">
      <div class="head has-text-centered">
        <h3>Human 01</h3>
      </div>
      <date-input ref="human01" :button="false"></date-input>
    </div>
    <div class="human02">
      <div class="head has-text-centered">
        <h3>Human 02</h3>
      </div>
      <date-input ref="human02" :button="false"></date-input>
    </div>
    <div class="loading" v-if="loading !== null">
      <pacman-loader :loading="loading" color="#30C39E" :width="125"></pacman-loader>          
    </div>
    <div class="result" v-show="loveExists">
      <transition name="fade">
        <div class="final">
          <h3>
            Your compatibility is:
            <span class="color--pri fs--2">{{ compatibility }}</span>
          </h3>
          <transition-group name="fade" mode="out-in" tag="div" class="compatibility">
            <font-awesome-icon v-for="(point, idx) in love" :key="idx" class="icon" icon="heart"/>
          </transition-group>
        </div>
      </transition>
    </div>
    <div class="submit">
      <button class="hov" @click="getLove()">Check your compatibility!</button>
    </div>
  </div>
</template>
<style lang='scss'>
