</style>
<script>
import DateInput from "./DateInput";
import { PacmanLoader } from '@saeris/vue-spinners'
export default {
  components: { DateInput, PacmanLoader },
  data() {
    return {
      love: [],
      signHuman01: "",
      signHuman02: "",
      loading: null,
      compatibility: ""
    };
  },
  mounted() {
    console.log("Love component mounted!");
  },
  methods: {
    getDates() {
      const human01 = this.$refs.human01.getDate();
      const human02 = this.$refs.human02.getDate();
      return [human01, human02];
    },
    getLove() {
      this.loading = true;
      const dates = this.getDates();
      const date01 = Object.values(dates[0]).join("-");
      const date02 = Object.values(dates[1]).join("-");
      // console.log(date01, date02);
      this.$refs.human01.getSign();
      this.$refs.human02.getSign();
      axios
        .get(`/match/${date01}/${date02}`)
        .then(
          response => (
            (this.love = response.data.loveCompatibility),
            (this.compatibility = response.data.signCompatibility),
            this.loading = false,
            console.log(response)
          )
        );
      this.$emit("getLove", this.love);
    }
  },
  computed: {
    loveExists() {
      return this.love !== undefined && this.love.length > 0 ? true : false;
    }
  }
};
</script>